<template>
  <div>
    <div v-for="item in list">
      <div class="data_colums_item">
        <div class="spilt_wrapper">
          <div class="data_colums_item_split"></div>
        </div>
        <div class="data_colums_item_container">
          <div class="data_colums_item_title">
            {{ item.title }}
          </div>
          <div class="data_colums_item_content">{{ item.content }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "HightlightLine",
  props: ["list"]
};
</script>
<style>
.spilt_wrapper {
  height: 24px;
  display: flex;
  align-items: center;
}
.data_colums_item {
  margin-bottom: 8px;
  line-height: 24px;
  display: flex;
}
.data_colums_item_split {
  background: black;
  height: 3px;
  width: 3px;
  border-radius: 3px;
  display: inline;
  margin-right: 5px;
}
.data_colums_item_title {
  font-weight: 600;
  font-size: 16px;
  margin-right: 5px;
  display: inline;
}
.data_colums_item_content {
  font-size: 16px;
  flex-grow: 1;
  display: inline;
}
</style>



