<template>
  <div style="position:relative">
    <div class="cite_button">
      <img width="12px" height="12px" src="./copy.png" />
      复制
    </div>
    <pre>
  <code v-html="info"></code>
  </pre>
  </div>
</template>
<script>
export default {
  name: "CiteInfo",
  props: ["info"]
};
</script>
<style scoped>
pre {
  background-color: #f6f8fa;
  border-radius: 3px;
  font-size: 85%;
  line-height: 1.45;
  overflow: auto;
  padding: 16px;
}
.cite_button {
  position: absolute;
  top: 5px;
  right: 5px;
  display: inline-block;
  font-size: 12px;
  margin-left: 10px;
  line-height: 12px;
  border-radius: 3px;
  padding: 7px 15px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
}
</style>



