<template>
  <div>
    <div class="card_info_item" v-for="(item, index) in list" :key="index">
      <div class="card_info_item_title">
        {{ item.title }}
      </div>
      <div class="card_info_item_content">
        <template v-if="index === 3">
          <a :href="item.content" target="_blank">{{ item.content }}</a>
        </template>
        <template v-else>
          {{ item.content }}
        </template>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ClassifyFormList",
  props: ["list"],
};
</script>
<style>
.card_info_item {
  background: white;
  padding: 8px;
  display: flex;
  margin-bottom: 8px;
}
.card_info_item_title {
  flex-shrink: 0;
  min-width: 30%;
  font-weight: 500;
}
.card_info_item_content {
  flex-grow: 1;
  text-align: right;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
  text-wrap-mode: nowrap;
}
</style>



