<template>
    <v-row>
      <v-col cols="12" class="dataset_title">
        <div class="dataset_name">
          {{ dataset_name }}
        </div>
        <div class="dataset_user"></div>
        <div class="dataset_key">
          <div class="dataset_key_item" v-for="item in keys">{{ item }}</div>
        </div>
        <div class="dataset_viewer">
          <div class="dataset_viewer_item" v-for="item in dataset_viewer">
            <div style="display: flex; align-items: center">
              <img width="12px" height="12px" :src="item.src" />
              <div class="dataset_viewer_title">{{ item.title }}</div>
              <div class="dataset_viewer_content">{{ item.content }}</div>
            </div>
          </div>
        </div>
      </v-col>
    </v-row>
</template>
<script>
export default {
  name: "Header",
  props: ["dataset_name", "keys", "dataset_viewer"],
};
</script>
<style>
.dataset_title {
  background-color: #159957;
  background-image: linear-gradient(120deg, #155799, #159957);
  padding: 20px 0;
  color: white;
  text-align: center;
}
.dataset_name {
  font-weight: bold;
  font-size: 30px;
  margin-bottom: 20px;
}
.dataset_user {
  font-size: 20px;
  font-weight: bold;
  font-weight: 500;
}
.dataset_key {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 8px;
}
.dataset_key_item {
  display: inline;
  margin-right: 16px;
}
.dataset_key_item:last-child {
  margin-right: 0;
}
.dataset_viewer {
  font-size: 16px;
}
.dataset_viewer_item {
  display: inline-block;
  margin-right: 16px;
}
.dataset_viewer_item:last-child {
  margin-right: 0;
}
.dataset_viewer_title {
  display: inline-block;
  font-size: 12px;
  margin: 0 2px;
  font-weight: bold;
}
.dataset_viewer_content {
  display: inline-block;
  font-size: 12px;
}
</style>



