<template>
  <v-row>
    <v-col :cols="splitCol" v-for="item in info">
      <div class="color_info_item">
        <div class="color_info_item_title">
          {{ item.title }}
        </div>
        <div class="color_info_item_content">
          {{ item.content }}
        </div>
      </div>
    </v-col>
  </v-row>
</template>
<script>
export default {
  name: "ColorCard",
  props: ["info", "splitCol"]
};
</script>
<style>
.color_info_item {
  padding: 8px;
  margin-right: 24px;
  width: 100%;
  border-left: 5px solid #0065a3;
  background: white;
  border-radius: 5px;
}
.color_info_item_title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 8px;
}
.color_info_item_content {
  font-size: 16px;
  line-height: 24px;
}
</style>



