<template>
  <Web
    :dataset_name="dataset_name"
    :discription="discription"
    :keys="keys"
    :fromInfo="fromInfo"
    :citeInfo="citeInfo"
    :code="code"
    :useInfo="useInfo"
    :datasets_split_info="datasets_split_info"
    :data_colums="data_colums"
  />
</template>
<script>
import Web from "../components/Classify/ClassifyDatasetWeb.vue";
export default {
  name: "Monitor",
  components: { Web },
  data() {
    return {
      dataset_name: "日常采集数据集",
      discription:
        "本数据专门为机器人时序数据分类训练创建。针对现有的时序数据数据集存在的时间序列短，数据存在污染等现象。本数据集对6台不同类型的6轴机器人，分别在不同的速率下，执行不同的任务，最终采集得到大约2亿条数据。每条数据包括一个时间点下机器人各个轴的力矩，轴旋转位置，角速度信息。",
      dataset_viewer: [
        //  { title: "访问量", content: "4", src: require("./view.png") },
        //  { title: "下载量", content: "1", src: require("./down_num.png") }
      ],
      tag_info_list: [
        { title: "访问量", content: "4" },
        { title: "下载量", content: "1" }
      ],
      keys: ["时序分类", "机器人", "多场景"],
      fromInfo: [
        { title: "发布机构", content: "北航ACT实验室" },
        { title: "发布时间", content: "2024/10/22" },
        { title: "更新时间", content: "2024/10/25" },
        {
          title: "下载地址",
          content: "https://download.act.buaa.edu.cn/robotData_classfication/"
        },
        { title: "数据集大小", content: "20GB" },
        { title: "存储格式", content: "csv" }
      ],
      useInfo: [
        { title: "样本点数目", content: "1亿" },
        { title: "相关任务", content: "机器人分类" },
        { title: "特征", content: "18" }
      ],
      datasets_split_info: [
        {
          title: "完整数据集",
          content: "6台机器人以不同速率运行不同动作运行结果",
          src: "https://download.act.buaa.edu.cn/robotData_classfication/"
        }
      ],
      citeInfo:
        "@misc{<br> title={日常采集数据集},<br> url={https://datasets.act.buaa.edu.cn/#/monitor},<br> author={北航ACT实验室},<br> year={2024}<br>}",

      data_colums: [
        { title: "time", content: "时间戳(ms)" },
        {
          title: "axle_moment?",
          content: "?取值0-5，代表机器人第?个轴的力矩(NM)"
        },
        {
          title: "axle_position?",
          content: "?取值0-5，代表机器人第?个轴的旋转位置(°)"
        },
        {
          title: "axle_speed?",
          content: "?取值0-5，代表机器人第?个轴的角速度(°/s)"
        }
      ]
    };
  }
};
</script>
<style></style>



