<template>
  <div>
    <template>
      <el-table :data="tableData" stripe style="width: 100%">
        <el-table-column prop="key" label="键" width="140" />
        <el-table-column prop="dim" label="维度" width="80" />
        <el-table-column prop="value" label="值" />
      </el-table>
    </template>
  </div>
</template>
<script>
export default {
  name: "HightlightLine",
  props: ["tableData"]
};
</script>
<style>
.spilt_wrapper {
  height: 24px;
  display: flex;
  align-items: center;
}
.data_colums_item {
  margin-bottom: 8px;
  line-height: 24px;
  display: flex;
}
.data_colums_item_split {
  background: black;
  height: 3px;
  width: 3px;
  border-radius: 3px;
  display: inline;
  margin-right: 5px;
}
.data_colums_item_title {
  font-weight: 600;
  font-size: 16px;
  margin-right: 5px;
  display: inline;
}
.data_colums_item_content {
  font-size: 16px;
  flex-grow: 1;
  display: inline;
}
</style>



