<template>
  <div class="data_body">
    <Header
      :dataset_name="dataset_name"
      :keys="keys"
      :dataset_viewer="dataset_viewer"
    />
    <v-row>
      <v-col cols="4">
        <Card title="简介" :content="discription" />
        <Card title="发布信息">
          <ClassifyFormList :list="fromInfo" />
        </Card>
        <Card title="使用方法">
          <Code :code="code" />
        </Card>
        <Card title="引用">
          <CiteInfo :info="citeInfo" />
        </Card>
      </v-col>
      <v-col cols="8">
        <Card title="数据集性质">
          <ColorCard :info="useInfo" :splitCol="4" />
        </Card>
        <Card title="数据信息">
          <TableLine v-if="tableData" :tableData="tableData" />
          <HightlightLine v-if="data_colums" :list="data_colums" />
        </Card>
        <Card title="数据集划分">
          <DownloadCard :list="datasets_split_info" />
        </Card>
        <Card title="实例视频">
          <div class="video-container">
            <div class="video-item">
              <video width="152" height="270" controls>
                <source
                  src="static/video/robotClassify/damo.mp4"
                  type="video/mp4"
                />
                您的浏览器不支持视频播放。
              </video>
              <p class="video-description">打磨机器人</p>
            </div>
            <div class="video-item">
              <video width="152" height="270" controls>
                <source
                  src="static/video/robotClassify/maduo.mp4"
                  type="video/mp4"
                />
                您的浏览器不支持视频播放。
              </video>
              <p class="video-description">码垛机器人</p>
            </div>
            <div class="video-item">
              <video width="152" height="270" controls>
                <source
                  src="static/video/robotClassify/zhuangpei.mp4"
                  type="video/mp4"
                />
                您的浏览器不支持视频播放。
              </video>
              <p class="video-description">装配机器人</p>
            </div>
          </div>
        </Card>
      </v-col>
    </v-row>
    <Footer />
  </div>
</template>
<script>
import PrismEditor from "../editor";
import Header from "../Header.vue";
import Card from "../common/Card.vue";
import ClassifyFormList from "./ClassifyFormList.vue";
import CiteInfo from "../common/CiteInfo.vue";
import ColorCard from "../common/ColorCard.vue";
import TableLine from "../TableLine.vue";
import HightlightLine from "../HightlightLine.vue";
import DownloadCard from "../common/DownloadCard.vue";
import Code from "../common/Code.vue";
import Footer from "../Footer.vue";
export default {
  name: "AppList",
  components: {
    PrismEditor,
    Header,
    Card,
    CiteInfo,
    ClassifyFormList,
    ColorCard,
    TableLine,
    HightlightLine,
    DownloadCard,
    Code,
    Footer,
  },
  props: [
    "dataset_name",
    "discription",
    "dataset_viewer",
    "keys",
    "fromInfo",
    "citeInfo",
    "code",
    "useInfo",
    "datasets_split_info",
    "tableData",
    "data_colums",
  ],
};
</script>
<style scoped>
.video-container {
  display: flex;
  justify-content: start;
  gap: 15px;
}

.video-item {
  display: flex;
  flex-direction: column; /* 让文字和视频竖向排列 */
  align-items: center; /* 水平居中对齐 */
  margin: 5px; /* 可以根据需要调整视频之间的间距 */
}

.video-description {
  margin-top: 5px; /* 文字与视频之间的间距 */
}
</style>



