<template>
  <div class="base_card">
    <div class="base_card_title">
      {{ title }}
    </div>
    <div class="base_card_content">
      {{ content }}
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "Card",
  props: ["title", "content"]
};
</script>
<style>
.base_card {
  width: 100%;
  padding: 8px 8px;
}
.base_card_title {
  font-size: 20px;
  font-weight: 600;
  border-bottom: 1px solid black;
  margin-bottom: 4px;
}
.base_card_content {
  font-size: 16px;
  line-height: 24px;
}
</style>



