<template>
  <div class="datasets_items">
    <div v-for="item in list" class="datasets_split_info">
      <div class="dataset_icon">
        <img height="40px" width="40px" src="./icon.png" />
      </div>
      <div class="datasets_title" @click="jumpUrl(item.src)">
        <div class="datasets_title_text">{{ item.title }}</div>
        <div class="datasets_title_icon">
          <div class="dropdown-content">下载</div>
          <img height="12px" width="12px" src="./download.png" />
        </div>
      </div>
      <div class="datasets_content">{{ item.content }}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "DownloadCard",
  props: ["list"],
  methods: {
    jumpUrl(src) {
      window.location.href = src;
    }
  }
};
</script>
<style>
.datasets_items {
  display: flex;
}
.datasets_split_info {
  padding: 8px;
  border: 1px solid rgb(218, 220, 224);
  border-radius: 8px;
  border-color: #1557997a;
  display: inline-block;
  width: 150px;
  height: 150px;
  background: white;
  margin-right: 20px;
}
.dataset_icon {
  text-align: center;
}
.datasets_title_icon {
  display: inline;
  position: relative;
}
.dropdown-content {
  position: absolute;
  opacity: 0;
  background-color: rgb(32, 33, 36);
  color: white;
  left: -12px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  padding: 2px 10px;
  font-size: 10px;
  border-radius: 5px;
  text-wrap: nowrap;
}
.datasets_title_icon:hover .dropdown-content {
  opacity: 0.95;
}
.datasets_title {
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}
.datasets_title_text {
  line-height: 24px;
  margin-right: 4px;
  display: inline-block;
}
.datasets_content {
  font-size: 12px;
}
</style>



