<template>
  <prism-editor
    class="my-editor height-300"
    v-model="code"
    :highlight="highlighter"
    :readonly="true"
    :line-numbers="true"
  ></prism-editor>
</template>
<script>
// import Prism Editor
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css";

// import highlighting library
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-core"; // 导入核心模块
import "prismjs/components/prism-python";

import "prismjs/themes/prism-tomorrow.css";

export default {
  name: "highLight",
  props: ["isReadonly", "isLineNumber", "language", "hightLightCode"],
  components: {
    PrismEditor
  },
  data: () => ({
    code:
      'with h5py.File(f"path/to/all_data.hdf5", "w-") as f:\\nsolutions = f["solutions"]'
  }),
  created() {
    this.code = this.hightLightCode;
  },
  methods: {
    highlighter(code) {
      return highlight(code, languages[this.language]);
    }
  }
};
</script>

<style>
/* required class */
.my-editor {
  /* we dont use `language-` classes anymore so thats why we need to add background and text color manually */
  background: #2d2d2d;
  color: #ccc;

  /* you must provide font-family font-size line-height. Example: */
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
  margin-top: calc(20 / 19.2 * 1vw);
}

/* optional class for removing the outline */
.prism-editor__textarea:focus {
  outline: none;
}

/* not required: */
.height-300 {
  height: calc(400 / 19.2 * 1vw);
}
</style>



